import React from 'react';

const PrintStar = () => {
    const [myNumber, setNumber] = React.useState('');
    const numberChange = e => {
        setNumber(e.currentTaget.value);
    };
    return (
        <div>
            <h2>PrintStar</h2>
            <h3>useState, useEffect, useRef   </h3>
            <hr />
            <label htmlFor="number">rownum: </label>
            <input id="number" type="text" onChange={numberChange} value={myNumber} />
            <hr />
            {myNumber}
        </div>
    );
};

export default PrintStar;